<template>
  <div>
    <div class="tb-header" style="height: auto">
      <span>历史数据</span>
      <el-form label-width="80px">
        <el-row>
          <el-col :span="6">
            <el-form-item label="传感器">
              <el-select v-model="SelectedID" placeholder="请选择">
                <el-option
                  v-for="(item,index) in SensorList"
                  :key="index"
                  :label="item.Name"
                  :value="item.ID"
                ></el-option>
              </el-select>

            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="开始时间">
              <el-date-picker type="date" placeholder="开始时间"   v-model="StartTime"></el-date-picker>

            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="结束时间">
              <el-date-picker type="date" placeholder="结束时间"  v-model="EndTime"></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item  >
            <el-button @click="Query">查询</el-button>
            </el-form-item>
          </el-col>
        </el-row>

      </el-form>
    </div>
    <div class="chart" id="Chart" style="width:100%;height:400px;"></div>
  </div>
</template>
<script>
  import axios from 'axios'
  import echarts from 'echarts'
  import {TimeFormat} from '../assets/js/Common'
  //  import {GetStore, SetStore} from '../vuex/store'
  export default {
    data() {
      return {
        DataList: [],
        StartTime: null,
        EndTime: null,
        SensorList: [],
        SelectedID: 0,

      }
    },
    methods: {
      Query() {
        var query = {
          ID: this.SelectedID,
          StartTime: this.StartTime,
          EndTime: this.EndTime
        }
        var sensor = null
        for (var i = 0; i < this.SensorList.length; i++) {
          if (this.SensorList[i].ID === this.SelectedID) {
            sensor = this.SensorList[i]
            break
          }
        }

        this.chart = echarts.init(document.getElementById('Chart'))
        axios.post('/api/querySensorHistory', query).then(response => {
          this.DataList = response.data.Data

          var x = new Array()
          var y = new Array()
          var z = new Array()
          for (var i = 0; i < this.DataList.length; i++) {
            x.push(TimeFormat(this.DataList[i].CreateTime))
            y.push(this.DataList[i].FirstValue)
            z.push(this.DataList[i].SecondValue)
          }
          this.chart.clear()
          this.chart.setOption({
            title: {text: sensor.Name + '历史数据'},
            tooltip: {},
            xAxis: {
              data: x
            },
            yAxis: {
              name: (sensor.SensorType === 0) ? '兆帕' : '千牛'
            },
            series: [{
              name: '前柱',
              type: 'line',
              data: y
            },
            {
              name: '后柱',
              type: 'line',
              data: z
            }]
          })
        })
      }
    },
    mounted (){
    axios.post('/api/getAllSensor', this.QueryForm).then(response => {
      this.SensorList = response.data.Data
      if (this.SensorList.length > 0) {
        this.SelectedID = this.SensorList[0].ID
      }
      this.StartTime = new Date()
      this.EndTime = new Date()
      this.Query()
    })
    }
  }
</script>




