<template>
  <el-container>
    <el-header class="fixed-header">
      <el-row type="flex" class="row-bg" style="height: inherit">
      <el-col :span="5">
        <span  class="minename">{{MineName}}</span>
      </el-col>
        <el-col :span="16">
          <el-menu :default-active="CurrentPath" class="el-menu-demo" :router="true"
            mode="horizontal" @select="selectMenu" background-color="#545c64" text-color="#fff" active-text-color="#ffd04b">
            <el-menu-item index="" @click="SystemConfigDialogVisible=true">系统配置</el-menu-item>
            <el-menu-item index="/SensorSet" >传感器配置</el-menu-item>
            <el-menu-item index="/SensorRealTimeData" >实时数据</el-menu-item>
            <el-menu-item index="/SensorHisData" >历史数据</el-menu-item>

            <!--<el-submenu index="4">-->
              <!--<template slot="title">历史</template>-->
              <!--<el-menu-item index="/FrameHis">支架</el-menu-item>-->
              <!--<el-menu-item index="/PillarHis" >支柱</el-menu-item>-->
            <!--</el-submenu>-->
            <!--<el-submenu index="5">-->
              <!--<template slot="title">报表</template>-->
              <!--<el-menu-item index="/FrameReport">支架</el-menu-item>-->
            <!--</el-submenu>-->
            <el-submenu index="6">
              <template slot="title">工具</template>
              <!--<el-menu-item index="/Com">串口通讯</el-menu-item>-->
              <!--<el-menu-item index="/Adb">手机通讯</el-menu-item>-->
              <el-menu-item index="" @click="BackupDialogVisible = true">备份</el-menu-item>
              <el-menu-item index="" @click="RestartSerialDialogVisible = true">重启串口</el-menu-item>
              <!--<el-menu-item index="602" @click="RestoreDialogVisible = true">还原</el-menu-item>-->
            </el-submenu>
            <el-menu-item index="" @click="HelpDialogVisible = true">帮助</el-menu-item>
            <!--<el-submenu index="7">-->
              <!--<template slot="title">帮助</template>-->
              <!--<el-menu-item index="/Help">帮助</el-menu-item>-->
              <!--<el-menu-item index="/About">关于</el-menu-item>-->
            <!--</el-submenu>-->
          </el-menu>
          <!--<el-menu class="el-menu-demo" mode="horizontal" :router="true" :default-active="CurrentPath" @select="selectMenu">-->

          <!--</el-menu>-->
        </el-col>
        <el-col :span="3">
          <div class="user">
            <a href="javascript:void(0)" @click="logout">退出</a>
            <span>{{UserName}}</span>
          </div>
        </el-col>
      </el-row>
    </el-header>
    <el-container>
      <el-aside width="30px">

      </el-aside>
      <el-main>
        <router-view/>
      </el-main>
    </el-container>

    <el-footer  class="fixed-footer" >

    </el-footer>
    <el-dialog title="备份" :visible.sync="BackupDialogVisible" width="30%" >
      <span>是否开始执行备份</span>
      <span slot="footer" class="dialog-footer">
    <el-button @click="BackupDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="doBackup">确 定</el-button>
  </span>
    </el-dialog>

    <el-dialog title="重启串口" :visible.sync="RestartSerialDialogVisible" width="30%" >
      <span>是否开确认重启串口</span>
      <span slot="footer" class="dialog-footer">
    <el-button @click="RestartSerialDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="doRestart">确 定</el-button>
  </span>
    </el-dialog>

    <el-dialog title="还原" :visible.sync="RestoreDialogVisible" width="30%" >
      <span>是否开始执行还原</span>
      <span slot="footer" class="dialog-footer">
    <el-button @click="RestoreDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="dorestore">确 定</el-button>
  </span>
    </el-dialog>

    <el-dialog title="帮助" :visible.sync="HelpDialogVisible" width="30%" >
      <span>煤矿压力监测管理系统v1.0</span>

    </el-dialog>

    <el-dialog title="系统配置" :visible.sync="SystemConfigDialogVisible"  width="500px" center custom-class="success-modal" :show-close="false" :close-on-click-model="false" :close-on-click-modal="false">
      <el-form   class="demo-ruleForm"  label-width="100px">
        <el-row>
          <el-form-item label="煤矿名称" prop="MineName">
            <el-input class="nameStyle"  v-model="ChangeMineName" style="width: 85%;"></el-input>
          </el-form-item>
          <el-form-item label="串口名称" prop="ComName">
            <el-input class="nameStyle" v-model="ComName" style="width: 85%;"></el-input>
          </el-form-item>
          <el-form-item label="波特率" prop="Baud">
            <el-input class="nameStyle" type="number" v-model="BaudRate" style="width: 85%;"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="Baud">
            <el-input class="nameStyle" type="password" v-model="Password" style="width: 85%;"></el-input>
          </el-form-item>
          <!--<el-form-item label="校验位" prop="Parity">-->
            <!--<el-select v-model="Parity" placeholder="请选择">-->
              <!--<el-option-->
                <!--v-for="item in ParityList"-->
                <!--:key="item.ID"-->
                <!--:label="item.Name"-->
                <!--:value="item.ID">-->
              <!--</el-option>-->
            <!--</el-select> </el-form-item>-->
        </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="SystemConfigSubmit()">确 定</el-button>
        <el-button @click="SystemConfigDialogVisible = false">取 消</el-button>
      </span>
    </el-dialog>
  </el-container>
</template>
<script>
  import {GetStore, SetStore} from '../vuex/store'
  import axios from 'axios'
  import PostBody from '../assets/js/PostBody'
  export default {
    data: function () {
      return {
        MineName: '',
        ChangeMineName: '',
        UserName: 'admin',
        CurrentPath: '',
        BackupDialogVisible: false,
        RestoreDialogVisible: false,
        RestartSerialDialogVisible: false,
        SystemConfigDialogVisible: false,
        HelpDialogVisible: false,
        ComName: '',
        BaudRate: 1200,
        Parity: 0,
        Password: '',
        //ParityList: [{ID: 0, Name: '无校验'}, {ID: 1, Name: '奇校验'}, {ID: 2, Name: '偶校验'}]
      }
    },
    methods: {
      logout() {
        SetStore('Token', 0)
        SetStore('UserName', '')
        SetStore('MineName', '')
        this.$router.push('/Login')
      },
      selectMenu(index) {
        SetStore('CurrentPath', index)
      },
      doBackup() {
        axios.post('/api/backup', null).then(response => {
          if (response.data.State === 1) {
            this.$message.success('备份完成')
            this.BackupDialogVisible = false
          } else {
            this.$message.error(response.data.Message)
          }
        })
      },
      doRestart() {
        axios.post('/api/restart', null).then(response => {
          if (response.data.State === 1) {
            this.$message.success('重启完成')
            this.RestartSerialDialogVisible = false
          } else {
            this.$message.error(response.data.Message)
          }
        })
      },
      dorestore() {
        axios.post('/api/restore', null).then(response => {
          if (response.data.State === 1) {
            this.$message.success('还原完成')
            this.RestoreDialogVisible = false
          } else {
            this.$message.error(response.data.Message)
          }
        })
      },
      SystemConfigSubmit() {

        // 更新
        axios.post('/api/updateSystemConfig', {
          MineName: this.ChangeMineName,
          ComName: this.ComName,
          BaudRate: this.BaudRate,
          Password: this.Password,
          //Parity: this.Parity
        }).then(response => {
          if (response.data.State === 1) {
            this.$message.success('修改成功')
            this.MineName = this.ChangeMineName
            SetStore('MineName', this.MineName)
            this.SystemConfigDialogVisible = false
            // 调用 刷新串口接口
          } else {
            this.$message.error(response.data.Messgae)
          }
        })
      },
      GetAlarm() {

      }
    },
    created() {
      axios.post('/api/querySystemConfig', null).then(response => {
        if (response.data.State === 1 && response.data.Data != null) {
          this.MineName = response.data.Data.MineName
          this.ChangeMineName = this.MineName
          this.ComName = response.data.Data.ComName
          this.BaudRate = response.data.Data.BaudRate
          this.Password = response.data.Data.Password
          //this.Parity = response.data.Data[0].Parity
          SetStore('MineName', this.MineName)
        }
      })
      this.UserName = GetStore('UserName')
      this.CurrentPath = GetStore('CurrentPath')
      if (this.CurrentPath === '') {
        this.CurrentPath = '/SensorSet'
      }
    }
  }
</script>

<style>
  .minename{
    text-align: center;
    vertical-align: middle;
    line-height:60px;
    font-size: 28px;
    color: #FFFFFF;
  }

  .user span, a{
    vertical-align: middle;
    line-height: 60px;
    text-align: right;
    font-size: 16px;
    color: #FFFFFF;
  }

  .user a {
    margin-left: 25px;
    float: right;
  }

  .user span {
    float: right;
  }







  .el-menu {
    background-color: rgba(19, 116, 157, 1);
  }
.el-menu--horizontal {
  float: left;
}

  .el-menu--horizontal .el-submenu .el-menu-item{
    background-color: rgba(19, 116, 157, 1);
    font-size: 16px;
    padding:0;
  }

  .el-menu--horizontal .el-menu-item {
  font-size: 16px;
  color: #ffffff;
}



.el-menu--horizontal>.el-menu-item.is-active, .el-menu--horizontal>.el-submenu .el-submenu__title.is-active {
  background-color: #0E5472;
  color: #ffffff;
}
  .el-menu--horizontal .el-submenu .el-submenu__title:focus, .el-menu--horizontal .el-submenu .el-submenu__title:hover {
    background-color: #0E5472;
    color: #ffffff;
  }

.el-menu--horizontal .el-menu-item:focus, .el-menu--horizontal .el-menu-item:hover {
  background-color: #0E5472;
  color: #ffffff;
}
  .el-menu--horizontal .el-submenu .el-submenu__title{
    color: #ffffff;
  }

</style>



