<template>
  <div>
    <div class="tb-header">
      <span>传感器配置</span>
      <el-button  @click="addOpen()" icon="el-icon-circle-plus-outline"  >新增</el-button>
      <el-button type="primary" @click="editOpen()" icon="el-icon-edit" >修改</el-button>
      <el-button type="danger" @click=" deleteOpen()" icon="el-icon-circle-close-outline">删除</el-button>
      <el-input placeholder="名称"  class="input-with-select" style="" v-model="QueryForm.Name" @keyup.enter.native="searchSubmit">
        <!--<i class="el-icon-search" slot="append"></i>-->
        <el-button slot="append" icon="el-icon-search" style="color: rgba(25, 158, 216, 1);" @click="searchSubmit"></el-button>
      </el-input>
    </div>

    <el-table :data="DataList" @selection-change="SelectionChange" tooltipEffect="dark" :row-class-name="tableRowClassName">
      <el-table-column type="selection"></el-table-column>
      <el-table-column prop="ID"  label="编号"> </el-table-column>
      <el-table-column label="名称" prop="Name"></el-table-column>
      <el-table-column label="安装位置" prop="WorkPlace">
      </el-table-column>
      <el-table-column prop="SensorType"  label="传感器类型">
        <template slot-scope="scope">
          {{ GetSensorType(scope.row.SensorType) }}
        </template>
      </el-table-column>

      <el-table-column prop="FirstValue"  label="前柱">
        <template slot-scope="scope">
          {{ GetFirstValue(scope.row.SensorType,scope.row.FirstValue) }}
        </template>
      </el-table-column>
      <el-table-column prop="SecondValue"  label="后柱">
        <template slot-scope="scope">
          {{ GetSecondValue(scope.row.SensorType,scope.row.SecondValue) }}
        </template>
      </el-table-column>
      <el-table-column prop="CreateTime"  label="创建时间">
        <template slot-scope="scope">
          {{ format(scope.row.CreateTime) }}
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="新增传感器" :visible.sync="addVisible"  width="500px" center custom-class="success-modal" :show-close="false" :close-on-click-model="false" :close-on-click-modal="false">
      <el-form :model="AddForm"  class="demo-ruleForm"  label-width="100px">
        <el-row>
          <el-form-item label="传感器编号" prop="ID">
            <el-input class="nameStyle" type="number" v-model="AddForm.ID" style="width: 85%;"></el-input>
          </el-form-item>
          <el-form-item label="传感器名称" prop="WorkPlace">
            <el-input class="nameStyle" v-model="AddForm.Name" style="width: 85%;"></el-input>
          </el-form-item>
          <el-form-item label="传感器位置" prop="WorkPlace">
            <el-input class="nameStyle" v-model="AddForm.WorkPlace" style="width: 85%;"></el-input>
          </el-form-item>

          <el-form-item label="传感器类型" prop="SensorType">
          <el-select v-model="AddForm.SensorType" placeholder="请选择">
            <el-option
              v-for="(item,index) in options"
              :key="index"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
          </el-form-item>

        </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addSubmit()">确 定</el-button>
        <el-button @click="addVisible = false">取 消</el-button>
      </span>
    </el-dialog>
    <el-dialog title="修改传感器" :visible.sync="editVisible"  width="500px" center custom-class="success-modal" :show-close="false" :close-on-click-model="false" :close-on-click-modal="false">
      <el-form :model="EditForm"  class="demo-ruleForm"  label-width="100px">
        <el-row>
          <el-form-item label="支架编号" prop="Name">
            <el-input class="nameStyle" type="number" v-model="EditForm.ID" :disabled="true" style="width: 85%;"></el-input>
          </el-form-item>
          <el-form-item label="支架名称" prop="Name">
            <el-input class="nameStyle" v-model="EditForm.Name" style="width: 85%;"></el-input>
          </el-form-item>
          <el-form-item label="安装位置" prop="InstallPosition">
            <el-input class="nameStyle"   v-model="EditForm.WorkPlace" style="width: 85%;"></el-input>

          </el-form-item>
          <el-form-item label="传感器类型" prop="SensorType">
            <el-select v-model="EditForm.SensorType" placeholder="请选择">
              <el-option
                v-for="(item,index) in options"
                :key="index"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editSubmit()">确 定</el-button>
        <el-button @click="editVisible = false">取 消</el-button>
      </span>
    </el-dialog>

    <el-dialog title="删除" :visible.sync="deleteVisible" width="500px" center custom-class="danger-modal" :show-close="false">
      <span>确定要<span style="color: red">删除</span>选中数据么?</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="delSubmit()">确 定</el-button>
        <el-button @click="deleteVisible = false">取 消</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
  import axios from 'axios'
  import {DateFormat} from '../assets/js/Common'
  //  import {GetStore, SetStore} from '../vuex/store'
  export default {
    data () {
      return {
        TableName: 'SensorSet',
        searchName: '',
        RowChecked: [],
        MultipleSelection: [],
        DataList: [],
        addVisible: false,
        deleteVisible: false,
        editVisible: false,
        QueryForm: {
          Name: ''
        },
        AddForm: {
          ID: null,
          WorkPlace: '',
          SensorType: 0,
          FirstValue: 0,
          SecondValue: 0,
          CreateTime: null
        },
        EditForm: {
          ID: null,
          WorkPlace: '',
          SensorType: 0,
          FirstValue: 0,
          SecondValue: 0,
          CreateTime: null
        },
        options: [{
          value: 0,
          label: '压力传感器'
        }, {
          value: 1,
          label: '锚杆传感器'
        }]
      }
    },
    methods: {
      format (ts) {
        return DateFormat(ts)
      },
      GetFirstValue(type,value){
        if (type === 0) {
          return value +'兆帕';
        } else {
          return value +'牵牛';
        }
      },
      GetSecondValue(type,value){
        if (type === 0) {
          return value+'兆帕';
        } else {
          return  '-';
        }
      },
      GetSensorType (type) {
        if (type === 0) {
          return '压力传感器';
        } else {
          return '锚杆传感器';
        }
      },
      validate (ob) {
        if (ob.ID === '') {
          this.$message.error('编号不能为空')
          return false
        }
        ob.ID = parseInt(ob.ID + '')
        if (ob.Name === '') {
          this.$message.error('传感器名称不能为空')
          return false
        }
        if (ob.WorkPlace === '') {
          this.$message.error('传感器工作地不能为空')
          return false
        }
        ob.SensorType = parseInt(ob.SensorType + '')
        return true
      },
      tableRowClassName ({row, rowIndex}) {
        if (this.RowChecked.indexOf(rowIndex) >= 0) {
          return 'checked-bg'
        } else {
          return ''
        }
      },
      SelectionChange (selection) {
        this.RowChecked = []
        this.MultipleSelection = selection
        let ids = []
        this.MultipleSelection.map((s, j) => {
          ids.push(s.ID)
        })
        this.RowChecked = ids
      },
      delSubmit () {
        var query = {
          'ID': this.RowChecked
        }
        axios.post('/api/deleteSensor', query).then(response => {
          if (response.data.State === 0) {
            this.$message.error('删除失败' + response.data.Message)
          } else {
            this.$message.success('删除成功')
            this.Query()
            this.deleteVisible = false
          }
        }).catch(err => {
          this.$message.error('删除失败' + err)
        })
      },
      addSubmit () {
        if (!this.validate(this.AddForm)) {
          return
        }
        axios.post('/api/insertSensor', this.AddForm).then(response => {
          if (response.data.State === 0) {
            this.$message.error('新增失败' + response.data.Message)
          } else {
            this.$message.success('新增成功')
            this.Query()
            this.addVisible = false
          }
        }).catch(err => {
          this.$message.error('新增失败' + err)
        })
      },
      addOpen(){
        this.EditForm.ID = 0
        this.EditForm.Name = ''
        this.EditForm.WorkPlace = ''
        this.EditForm.SensorType = 0
        this.addVisible = true
      },
      editOpen () {
        if (this.RowChecked.length === 0) {
          this.$message.error('请选中传感器')
          return
        }
        if (this.RowChecked.length > 1) {
          this.$message.error('请选中单个传感器')
          return
        }
        var row = this.MultipleSelection[0]
        this.EditForm.ID = row.ID
        this.EditForm.Name = row.Name
        this.EditForm.WorkPlace = row.WorkPlace
        this.EditForm.SensorType = row.SensorType
        this.editVisible = true
      },
      deleteOpen () {
        if (this.RowChecked.length === 0) {
          this.$message.error('请选中传感器')
          return
        }
        this.deleteVisible = true
      },
      editSubmit () {
        if (!this.validate(this.EditForm)) {
          return
        }
        axios.post('/api/updateSensor', this.EditForm).then(response => {
          if (response.data.State === 0) {
            this.$message.error('修改失败' + response.data.Message)
          } else {
            this.$message.success('修改成功')
            this.Query()
            this.editVisible = false
          }
        }).catch(err => {
          this.$message.error('修改失败' + err)
        })
      },
      searchSubmit () {
        this.Query()
      },
      Query () {
        axios.post('/api/querySensor', this.QueryForm).then(response => {
          this.DataList = response.data.Data
        })
      }
    },
    created () {
      this.Query()
    }
  }
</script>




