<template>
  <div>
    <el-form label-width="80px">
      <el-row>
    <div class="tb-header">
      <span>实时数据</span>
      <el-form label-width="80px">
        <el-row>
          <el-col :span="6">
            <el-form-item label="传感器">
              <el-select v-model="SelectedIndex" placeholder="请选择">
                <el-option
                  v-for="(item,index) in options"
                  :key="index"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item  >
              <el-button @click="Query">查询</el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>

    </div>
      </el-row>
      <el-row>
    <div class="chart" id="Chart" style="width:100%;height:400px;"></div>
      </el-row>
    </el-form>
  </div>
</template>
<script>
  import axios from 'axios'
  import echarts from 'echarts'
  //  import {DateFormat} from '../assets/js/Common'
  //  import {GetStore, SetStore} from '../vuex/store'
  export default {
    data() {
      return {
        DataList: [],
        SelectedIndex: 0,
        options: [{
          value: 0,
          label: '压力传感器'
        }, {
          value: 1,
          label: '锚杆传感器'
        }]
      }
    },
    methods: {

      // init() {
      //   const self = this;//因为箭头函数会改变this指向，指向windows。所以先把this保存
      //   setTimeout(() => {
      //     window.onresize = function() {
      //       self.chart = echarts.init(document.getElementById('Chart'));
      //       self.chart.resize();
      //     }
      //   },20)
      // },
      Query() {
        var query = {
          ID: this.SelectedIndex
        }


        this.chart = echarts.init(document.getElementById('Chart'))
        axios.post('/api/querySensorRealTime', query).then(response => {
          this.DataList = response.data.Data

          var x = new Array()
          var y = new Array()
          var z = new Array()
          for (var i = 0; i < this.DataList.length; i++) {
            x.push(this.DataList[i].Name)
            y.push(this.DataList[i].FirstValue)
            z.push(this.DataList[i].SecondValue)
          }
          this.chart.clear()

          // var myChart = echarts.init(document.getElementById('Chart'))
          // // 基于准备好的dom，初始化echarts实例
          if (query.ID == 0) {
            this.chart.setOption({
              title: {text: '实时数据'},
              tooltip: {},
              xAxis: {
                data: x
              },
              yAxis: {
                name: '兆帕'
              },
              series: [{
                name: '前柱',
                type: 'bar',
                data: y,
                color: 'red',
                barWidth: '20px'
              },
                {
                  name: '后柱',
                  type: 'bar',
                  data: z,
                  color: 'blue',
                  barWidth: '20px'
                }]
            })
          }
          else {
            this.chart.setOption({
              title: {text: '实时数据'},
              tooltip: {},
              xAxis: {
                data: x
              },
              yAxis: {
                name: '千牛'
              },
              series: [{
                name: '前柱',
                type: 'bar',
                data: y,
                color: 'red',
                barWidth: '20px'
              }]
            })
          }

        })
      }
    },
    created() {

    },
    mounted() {
      this.Query()
      // this.init()
    }
  }
</script>




