import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/views/Main'
import Login from '@/views/Login'
import SensorHisData from '@/components/SensorHisData'
import SensorSet from '@/components/SensorSet'
import SensorRealTimeData from '@/components/SensorRealTimeData'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/Main',
      name: 'Main',
      redirect: '/Main',
      component: Main,
      children: [
        {
          path: '/SensorHisData',
          name: 'SensorHisData',
          component: SensorHisData,
          hidden: true
        },
        {
          path: '/SensorSet',
          name: 'SensorSet',
          component: SensorSet,
          hidden: true
        },
        {
          path: '/SensorRealTimeData',
          name: 'SensorRealTimeData',
          component: SensorRealTimeData,
          hidden: true
        }
      ]
    }, {
      path: '/Login',
      name: 'Login',
      component: Login,
      hidden: true
    }
  ]
})



